/* eslint-disable */
import React, { useState, useEffect } from 'react';
import { Helmet } from 'react-helmet';
import {
  Box, colors,
  Container,
  Grid
} from '@material-ui/core';
import LatestSubscriptions from 'src/components/dashboard/LatestSubscriptions';
import Sales from 'src/components/dashboard/Sales';
import TotalCustomers from 'src/components/dashboard/TotalCustomers';
import PremiumCustomers from "../components/dashboard/PremiumCustomers";
import LiquidProfit from "../components/dashboard/LiquidProfit";
import PayPalComissions from "../components/dashboard/PayPalComissions";
import LatestActions from 'src/components/dashboard/LatestActions';
import {HOST} from "../host";
import axios from 'axios';

const Dashboard = () => {

  const [dashboardData, setDashboardData] = useState({});
  const [chartDataset, setChartDataset] = useState({dataset: []})
  const [date, setDate] = useState(new Date())

  const getRecurringActions = async () => {
    console.log(new Date().getUTCSeconds())
    await axios.get(`${HOST}/custom-admin-api/get-actions/5/`)
      .then((response) => {
        setDashboardData(dashboardData => ({...dashboardData, latest_actions: response.data}))
      });
  }

  const handleResponse = (data) => {
    setDashboardData(data)
    const tempData = { datasets: [
        {
          backgroundColor: colors.blueGrey[500],
          data: [],
          label: 'Novos Utilizadores'
        },
        {
          backgroundColor: colors.red[500],
          data: [],
          label: 'Aplicaao Aberta'
        },
        {
          backgroundColor: colors.indigo[500],
          data: [],
          label: 'Artigos Abertos'
        },
        {
          backgroundColor: colors.deepPurple[500],
          data: [],
          label: 'API Requests totais'
        },
      ],
      labels: []
    }
    // Sets chart data
    for (let i=0; i < data.daily_statistics.length; i++) {
      const date = new Date(data.daily_statistics[i].creation_date)
      tempData.datasets[0].data.push(data.daily_statistics[i].new_devices)
      tempData.datasets[1].data.push(data.daily_statistics[i].app_launched_count)
      tempData.datasets[2].data.push(data.daily_statistics[i].article_entries)
      tempData.datasets[3].data.push(data.daily_statistics[i].mobile_api_requests)
      tempData.labels.push(`${date.toLocaleDateString()}`)
    }
    setChartDataset(tempData)
  }

  useEffect(() => {
    axios.get(`${HOST}/custom-admin-api/get-data-for-dashboard/`).then((response) => handleResponse(response.data));
    // Creates an interval which will update the current data every minute
    // This will trigger a rerender every component that uses the useDate hook.
    const timer = setInterval(() => setDate(new Date()), 1000);
    const timerAPI = setInterval(() => getRecurringActions() , 5000);
    // Return a funtion to clear the timer so that it will stop being called on unmount
    return () => {
      clearInterval(timerAPI)
      clearInterval(timer)
    };
  }, []);

  return (
    <>
      <Helmet>
        <title>Direito Digital | Admin</title>
      </Helmet>
      <Box
        sx={{
          backgroundColor: 'background.default',
          minHeight: '100%',
          py: 3
        }}
      >
        <Container maxWidth={false}>
          <Grid
            container
            spacing={3}
          >
            <Grid
              item
              lg={3}
              sm={6}
              xl={3}
              xs={12}
            >
              <TotalCustomers totalCustomers={dashboardData.total_customers} todayCustomers={dashboardData.daily_new_devices_count} />
            </Grid>
            <Grid
              item
              lg={3}
              sm={6}
              xl={3}
              xs={12}
            >
              <PremiumCustomers premiumCustomers={dashboardData.premium_customers} percentage={(dashboardData.premium_customers / dashboardData.total_customers) * 100} />
            </Grid>
            <Grid
              item
              lg={3}
              sm={6}
              xl={3}
              xs={12}
            >
              <LiquidProfit liquidProfit={dashboardData.liquid_income} sx={{ height: '100%' }}/>
            </Grid>
            <Grid
              item
              lg={3}
              sm={6}
              xl={3}
              xs={12}
            >
              <PayPalComissions paypalComissions={dashboardData.paypal_comissions} percentage={(dashboardData.paypal_comissions / dashboardData.brute_income) * 100} sx={{ height: '100%' }} />
            </Grid>
            <Grid
              item
              lg={6}
              md={12}
              xl={7}
              xs={12}
            >
              <Sales dataset={chartDataset} />
            </Grid>
            <Grid
              item
              lg={6}
              md={6}
              xl={5}
              xs={12}
            >
              { dashboardData.latest_actions ? <LatestActions date={date} data={dashboardData.latest_actions} sx={{ height: '100%' }} /> : null }
            </Grid>
            <Grid
              item
              style={{width: "100%"}}
            >
              { dashboardData.latest_subscriptions ? <LatestSubscriptions latestSubscriptions={dashboardData.latest_subscriptions} /> : null}
              {/* <LatestProducts sx={{ height: '100%' }} /> */ }
            </Grid>
          </Grid>
        </Container>
      </Box>
    </>
  );
};

export default Dashboard;
