/* eslint-disable */
import React, {useState, useEffect} from 'react'
import {Helmet} from "react-helmet";
import {Box, Card, Container, Grid, Table, TableBody, TableCell, TableHead, TablePagination, TableRow, Typography} from "@material-ui/core";
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import PerfectScrollbar from "react-perfect-scrollbar";
import EditArticleDialog from "../components/product/EditArticleDialog";
import Alert from '@material-ui/lab/Alert';
import CheckCircleOutlineIcon from "@material-ui/icons/CheckCircleOutline";
import ArticleListToolbar from "../components/product/ArticleListToolbar";
import NewArticleDialog from "../components/product/NewArticleDialog";
import axios from "axios";
import {HOST} from "../host";
import {csrfRequest} from "../utils/csrf_request";


const ModifyCategory = (props) => {

  const [page, setPage] = useState(1);
  const [count, setCount] = useState(0);
  const [articles, setArticles] = useState([]);
  const [menuItems, setMenuItems] = useState([]);
  const [openDialog, setOpenDialog] = useState(false);
  const [openNewDialog, setOpenNewDialog] = useState(false);
  const [successMessage, setSuccessMessage] = useState({active: false, message: null});
  const [detailArticle, setDetailArticle] = useState({})
  const [newTitleInput, setNewTitleInput] = useState(null)
  const [newBodyInput, setNewBodyInput] = useState(null)

  // Gets ID from URL
  const getId = () => {
    if (window.location.pathname.endsWith('/')) {
      return window.location.pathname.split('/')[window.location.pathname.split('/').length - 2]
    } else {
      return window.location.pathname.split('/')[window.location.pathname.split('/').length - 1]
    }
  }

  // Called when Edit Dialog is open (article row is clicked)
  const handleOpenDialog = async (id) => {
    await axios.get(`${HOST}/custom-admin-api/get-detail-article/${id}/`).then((response) => {
      setDetailArticle(response.data)
      setOpenDialog(true)
    })
  }

  const handleNewOpenDialog = () => { setOpenNewDialog(true) }


  // Called when Save Button is clicked on Dialog
  const saveButton = () => {

    const saveButtonCallback = (response, status) => {
      if (status === 200) {
        setOpenDialog(false)
        setSuccessMessage(response)
      }
    }
    csrfRequest('POST', `${HOST}/custom-admin-api/save-article/`, saveButtonCallback, {title: detailArticle.title, body: detailArticle.body, article_id: detailArticle.id})
  }

  // Calls when Create Button is clicked on Dialog
  const createButton = () => {

    const createButtonCallback = (response, status) => {
      setOpenNewDialog(false)
      setSuccessMessage(response)
    }
    csrfRequest('POST', `${HOST}/custom-admin-api/create-article/`, createButtonCallback, {title: newTitleInput, body: newBodyInput, category_id: getId()})
  }

  const deleteButton = () => {

    const deleteButtonCallback = (response, status) => {
      setOpenDialog(false)
      setSuccessMessage(response)
    }
    csrfRequest('POST', `${HOST}/custom-admin-api/delete-article/`, deleteButtonCallback, {id: detailArticle.id})
  }

  // Keeps track of input title field
  const onTitleChange = (title) => { setDetailArticle({title: title, body: detailArticle.body, id: detailArticle.id}) }

  // Keeps track of input body field
  const onBodyChange = (body) => { setDetailArticle({title: detailArticle.title, body: body, id: detailArticle.id}) }

  // Called when Dialog is closed
  const handleCloseDialog = (which_one) => {
    if (which_one === 'EDIT') {
      setOpenDialog(false);
      setDetailArticle({})
    } else if (which_one === 'NEW') {
      setOpenNewDialog(false)
    }
  };

  const handlePageChange = (event, newPage) => {
    axios.get(`${HOST}/custom-admin-api/get-articles/${getId()}/?page=${newPage}`).then((response) => {
      setArticles(response.data.results)
      setPage(newPage)
    });
  };

  useEffect(() => {
    axios.get(`${HOST}/custom-admin-api/get-articles/${getId()}/?page=${page}`).then((response) => {
      setArticles(response.data.results)
      setCount(response.data.count)
      let i = 1;
      const tempArray = []
      while (i <= Math.ceil(response.data.count / 100)) {
        tempArray.push(<MenuItem value={i}>{i}</MenuItem>)
        i = i + 1
      }
      setMenuItems(tempArray)
    });
  }, [])
  return (
    <>
      <Helmet>
        <title>Admin</title>
      </Helmet>

      <Box
        sx={{
          backgroundColor: 'background.default',
          minHeight: '100%',
          py: 3
        }}
      >

        <Container maxWidth={false}>

          <ArticleListToolbar addArticleCallback={handleNewOpenDialog} />

          <Box sx={{ pt: 3 }}>


            <NewArticleDialog updateTitleFunc={setNewTitleInput} updateBodyFunc={setNewBodyInput} dialogStatus={openNewDialog} createCallback={createButton} closeFunc={handleCloseDialog}/>

            <EditArticleDialog handleTitleChange={onTitleChange} handleBodyChange={onBodyChange} article={detailArticle} dialogStatus={openDialog} saveCallback={saveButton} closeFunc={handleCloseDialog} deleteFunc={deleteButton}/>

            {successMessage.active === true ? <Alert onClose={() => {setSuccessMessage(false)}} style={{backgroundColor: "#bbfabb", marginBottom: 20}} iconMapping={{ success: <CheckCircleOutlineIcon fontSize="inherit" /> }}>
              {successMessage.message}
            </Alert> : null}

            <Card>
              <PerfectScrollbar>
                <Box sx={{ minWidth: 1050 }}>

                  <Table>
                    <TableHead>
                      <TableRow>

                        <TableCell>
                          <FormControl>
                            <InputLabel id="demo-simple-select-label">Page</InputLabel>
                            <Select
                              labelId="demo-simple-select-label"
                              id="demo-simple-select"
                              value={page}
                              onChange={(event) => handlePageChange(event, event.target.value)}
                            >

                              {menuItems.map((item) => {
                                return item
                              })}
                            </Select>
                          </FormControl>
                        </TableCell>

                      </TableRow>
                    </TableHead>
                    <TableBody>
                      {articles.map((article) => (
                        <TableRow
                          hover
                          key={article.id}
                          onClick={() =>  handleOpenDialog(article.id)}
                        >
                          <TableCell>
                            <Box
                              sx={{
                                alignItems: 'center',
                                display: 'flex'
                              }}
                            >
                              <Grid item>

                              </Grid>
                              <Typography
                                color="textPrimary"
                                variant="body1"
                                style={{marginLeft: 10}}
                              >
                                {article.title}
                              </Typography>
                            </Box>
                          </TableCell>

                        </TableRow>
                      ))}
                    </TableBody>
                  </Table>
                </Box>
              </PerfectScrollbar>
              <TablePagination
                component="div"
                count={count}
                onPageChange={handlePageChange}
                page={page}
                rowsPerPage={100}
              />
            </Card>
          </Box>
        </Container>
      </Box>
    </>
  )
}

export default ModifyCategory;
