/* eslint-disable */
import { useState } from 'react';
import PropTypes from 'prop-types';
import moment from 'moment';
import PerfectScrollbar from 'react-perfect-scrollbar';
import {
  Avatar,
  Box,
  Card,
  Checkbox, Chip, Grid,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TablePagination,
  TableRow,
  Typography
} from '@material-ui/core';
import { Icon } from '@iconify/react';
import {indigo} from "@material-ui/core/colors";
import AppleIcon from '@material-ui/icons/Apple';

const icons = {
  Tablet: <Icon icon="flat-color-icons:tablet-android" style={{width: 50, height: 50}} />,
  Handset: <Icon icon="flat-color-icons:phone-android" style={{width: 50, height: 50}} />,
  samsung: <Avatar sx={{backgroundColor: indigo[600], height: 35, width: 85, transform: "rotate(-10deg)"}}><Icon icon="cib:samsung" style={{width: 70, height: 70, color: "white", transform: "rotate(10deg)"}} /></Avatar>,
  Redmi: <Icon icon="simple-icons:xiaomi" style={{width: 50, height: 50, color: "#FF6B03"}} />,
  Apple: <AppleIcon style={{width: 50, height: 50, color: "#000000"}}/>,
  google: <Icon icon="grommet-icons:google" style={{width: 50, height: 50}} />
}

const DeviceListResults = ({ devices, ...rest }) => {

  const [selectedCustomerIds, setSelectedCustomerIds] = useState([]);
  const [limit, setLimit] = useState(10);
  const [page, setPage] = useState(0);
  const handleSelectAll = (event) => {
    let newSelectedCustomerIds;

    if (event.target.checked) {
      newSelectedCustomerIds = devices.map((customer) => customer.id);
    } else {
      newSelectedCustomerIds = [];
    }

    setSelectedCustomerIds(newSelectedCustomerIds);
  };

  const handleSelectOne = (event, id) => {
    const selectedIndex = selectedCustomerIds.indexOf(id);
    let newSelectedCustomerIds = [];

    if (selectedIndex === -1) {
      newSelectedCustomerIds = newSelectedCustomerIds.concat(selectedCustomerIds, id);
    } else if (selectedIndex === 0) {
      newSelectedCustomerIds = newSelectedCustomerIds.concat(selectedCustomerIds.slice(1));
    } else if (selectedIndex === selectedCustomerIds.length - 1) {
      newSelectedCustomerIds = newSelectedCustomerIds.concat(selectedCustomerIds.slice(0, -1));
    } else if (selectedIndex > 0) {
      newSelectedCustomerIds = newSelectedCustomerIds.concat(
        selectedCustomerIds.slice(0, selectedIndex),
        selectedCustomerIds.slice(selectedIndex + 1)
      );
    }

    setSelectedCustomerIds(newSelectedCustomerIds);
  };

  const handleLimitChange = (event) => {
    setLimit(event.target.value);
  };

  const handlePageChange = (event, newPage) => {
    setPage(newPage);
  };

  return (
    <Card {...rest}>
      <PerfectScrollbar>
        <Box sx={{ minWidth: 1050 }}>
          <Table>
            <TableHead>
              <TableRow>
                <TableCell padding="checkbox">
                  <Checkbox
                    checked={selectedCustomerIds.length === devices.length}
                    color="primary"
                    indeterminate={
                      selectedCustomerIds.length > 0
                      && selectedCustomerIds.length < devices.length
                    }
                    onChange={handleSelectAll}
                  />
                </TableCell>
                <TableCell>
                  Brand
                </TableCell>
                <TableCell>
                  Status
                </TableCell>
                <TableCell>
                  Device Type
                </TableCell>
                <TableCell>
                  Registration date
                </TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {devices.slice(0, limit).reverse().map((device) => (
                <TableRow
                  hover
                  key={device.id}
                  selected={selectedCustomerIds.indexOf(device.id) !== -1}
                >
                  <TableCell padding="checkbox">
                    <Checkbox
                      checked={selectedCustomerIds.indexOf(device.id) !== -1}
                      onChange={(event) => handleSelectOne(event, device.id)}
                      value="true"
                    />
                  </TableCell>
                  <TableCell>
                    <Box
                      sx={{
                        alignItems: 'center',
                        display: 'flex'
                      }}
                    >
                      <Grid item>

                          {icons[device.brand]}

                      </Grid>
                      <Typography
                        color="textPrimary"
                        variant="body1"
                        style={{marginLeft: 10}}
                      >
                        {device.model}
                      </Typography>
                    </Box>
                  </TableCell>
                  <TableCell>
                    <Chip
                      color="primary"
                      style={{backgroundColor: (device.status === 'PREMIUM' ? '#00ac2d' : '#ff8e00')}}
                      label={device.status}
                      size="small"
                    />
                  </TableCell>
                  <TableCell>
                    {icons[device.type]}
                  </TableCell>
                  <TableCell>
                    {moment(device.first_time_install).format('DD/MM/YYYY')}
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </Box>
      </PerfectScrollbar>
      <TablePagination
        component="div"
        count={devices.length}
        onPageChange={handlePageChange}
        onRowsPerPageChange={handleLimitChange}
        page={page}
        rowsPerPage={limit}
        rowsPerPageOptions={[5, 10, 25]}
      />
    </Card>
  );
};

DeviceListResults.propTypes = {
  device: PropTypes.array.isRequired
};

export default DeviceListResults;
