/* eslint-disable */
import React, {useState, useEffect} from "react";
import { Helmet } from 'react-helmet';
import { Box, Container } from '@material-ui/core';
import DeviceListResults from 'src/components/customer/DeviceListResults';
import DeviceListToolBar from 'src/components/customer/DeviceListToolBar';
import customers from 'src/__mocks__/customers';
import { HOST } from "../host";
import axios from "axios";

const DeviceList = () => {

  const [devices, setDevices] = useState([])

  useEffect(() => {
    axios.get(`${HOST}/custom-admin-api/get-devices/`).then((response) => setDevices(response.data));
  }, [])

  return (
    <>
      <Helmet>
        <title>Devices | Admin</title>
      </Helmet>
      <Box
        sx={{
          backgroundColor: 'background.default',
          minHeight: '100%',
          py: 3
        }}
      >
        <Container maxWidth={false}>
          <DeviceListToolBar />
          <Box sx={{ pt: 3 }}>
            <DeviceListResults devices={devices} />
          </Box>
        </Container>
      </Box>
    </>
  )
};

export default DeviceList;
