/* eslint-disable */
import { useEffect } from 'react';
import { Link as RouterLink, useLocation } from 'react-router-dom';
import PropTypes from 'prop-types';
import {
  Avatar,
  Box,
  Button,
  Divider,
  Drawer,
  Hidden,
  List,
  Typography
} from '@material-ui/core';
import {
  AlertCircle as AlertCircleIcon,
  BarChart as BarChartIcon,
  Lock as LockIcon,
  Settings as SettingsIcon,
  ShoppingBag as ShoppingBagIcon,
  User as UserIcon,
  UserPlus as UserPlusIcon,
  Users as UsersIcon
} from 'react-feather';
import NavItem from './NavItem';
import PhoneIphoneIcon from '@material-ui/icons/PhoneIphone';
import CategoryIcon from '@material-ui/icons/Category';
import { MAIN_URL } from "../host";

const user = {
  avatar: 'https://cdn-icons-png.flaticon.com/512/1795/1795606.png',
  jobTitle: 'Pgina de Admin',
  name: 'Direito Digital'
};

const items = [
  {
    href: `/${MAIN_URL}/dashboard/`,
    icon: BarChartIcon,
    title: 'Dashboard'
  },
  {
    href: `/${MAIN_URL}/devices/`,
    icon: PhoneIphoneIcon,
    title: 'Devices'
  },
  {
    href: `/${MAIN_URL}/categories/`,
    icon: CategoryIcon,
    title: 'Categories'
  },
  {
    href: `/${MAIN_URL}/account/`,
    icon: UserIcon,
    title: 'Account'
  },
  {
    href: `/${MAIN_URL}/settings/`,
    icon: SettingsIcon,
    title: 'Settings'
  },
  {
    href: '/login/',
    icon: LockIcon,
    title: 'Login'
  },
  {
    href: '/register/',
    icon: UserPlusIcon,
    title: 'Register'
  },
  {
    href: '/404/',
    icon: AlertCircleIcon,
    title: 'Error'
  }
];

const DashboardSidebar = ({ onMobileClose, openMobile }) => {
  const location = useLocation();
  useEffect(() => {
    if (openMobile && onMobileClose) {
      onMobileClose();
    }
  }, [location.pathname]);

  const content = (
    <Box
      sx={{
        display: 'flex',
        flexDirection: 'column',
        height: '100%'
      }}
    >
      <Box
        sx={{
          alignItems: 'center',
          display: 'flex',
          flexDirection: 'column',
          p: 2
        }}
      >
        <Avatar
          component={RouterLink}
          src={user.avatar}
          sx={{
            cursor: 'pointer',
            width: 64,
            height: 64
          }}
          to={`/${MAIN_URL}/dashboard/`}
        />
        <Typography
          color="textPrimary"
          variant="h5"
        >
          {user.name}
        </Typography>
        <Typography
          color="textSecondary"
          variant="body2"
        >
          {user.jobTitle}
        </Typography>
      </Box>
      <Divider />
      <Box sx={{ p: 2 }}>
        <List>
          {items.map((item) => (
            <NavItem
              href={item.href}
              key={item.title}
              title={item.title}
              icon={item.icon}
            />
          ))}
          <hr/>
          {JSON.parse(localStorage.getItem('sidebar_categories')) ? JSON.parse(localStorage.getItem('sidebar_categories')).map((item) => (
            <NavItem
              onClick={() => {window.location.pathname = `/${MAIN_URL}/categories/${item.id}/`}}
              href={`/${MAIN_URL}/categories/${item.id}/`}
              key={item.id}
              title={item.name}
              icon={ShoppingBagIcon}
            />
          )) : null}

        </List>
      </Box>
    </Box>
  );

  return (
    <>
      <Hidden lgUp>
        <Drawer
          anchor="left"
          onClose={onMobileClose}
          open={openMobile}
          variant="temporary"
          PaperProps={{
            sx: {
              width: 256
            }
          }}
        >
          {content}
        </Drawer>
      </Hidden>
      <Hidden lgDown>
        <Drawer
          anchor="left"
          open
          variant="persistent"
          PaperProps={{
            sx: {
              width: 256,
              top: 64,
              height: 'calc(100% - 64px)'
            }
          }}
        >
          {content}
        </Drawer>
      </Hidden>
    </>
  );
};

DashboardSidebar.propTypes = {
  onMobileClose: PropTypes.func,
  openMobile: PropTypes.bool
};

DashboardSidebar.defaultProps = {
  onMobileClose: () => { },
  openMobile: false
};

export default DashboardSidebar;
