import React, {useState} from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import CloseIcon from '@material-ui/icons/Close';
import Slide from '@material-ui/core/Slide';
import TextField from "@material-ui/core/TextField";
import {Grid} from "@material-ui/core";

const useStyles = makeStyles((theme) => ({
  appBar: {
    position: 'relative',
  },
  title: {
    marginLeft: theme.spacing(2),
    flex: 1,
  },
}));

const styles = {
  container: {
    display: 'flex',
    flexWrap: 'wrap',
  },
  textField: {
    width: 300,
    margin: 100,
  },
//style for font size
  resize:{
    fontSize:50
  },
}


const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

export default function NewArticleDialog(props) {
  const classes = useStyles();

  return (
    <div>
      <Dialog fullScreen open={props.dialogStatus} onClose={() => props.closeFunc('NEW')} TransitionComponent={Transition}>
        <AppBar className={classes.appBar}>
          <Toolbar>
            <IconButton edge="start" color="inherit" onClick={() => props.closeFunc('NEW')} aria-label="close">
              <CloseIcon />
            </IconButton>
            <Typography variant="h6" className={classes.title}>
              Close
            </Typography>
            <Button autoFocus color="inherit" onClick={props.createCallback}>
              Create
            </Button>
          </Toolbar>
        </AppBar>
        <div style={{backgroundColor: "#ffffff", border:"3px solid #5664D2", borderBottom: "25px solid #5664D2",
          boxShadow: "0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19)",
          marginTop: 100, marginLeft: 30, marginRight: 30, padding: 30, borderRadius: 10}}>
          <Grid
            container
            direction="column"
            justifyContent="space-between"
            alignItems="stretch">

            <TextField variant="standard" multiline={true} onChange={(event) => props.updateTitleFunc(event.target.value)} inputProps={{style: {fontSize: 25, lineHeight: 1}}} style={{marginBottom: 20}}/>

            <TextField variant='standard' multiline={true} onChange={(event) => props.updateBodyFunc(event.target.value)} inputProps={{style: {fontSize: 25, lineHeight: "36px"}, outline: "none"}} style={{paddingLeft: 10,paddingTop: 36, border: "3px solid #5664D2", margin: 0,background: "repeating-linear-gradient(#fdffd1, #fdffd1 35px, #0084c2 35px, #0084c2 36px)"}} />

          </Grid>
        </div>
      </Dialog>
    </div>
  );
}
