import {
  Card,
  CardContent,
  CardHeader,
  Divider,
  useTheme, Table, TableHead, TableRow, TableCell, TableBody, Chip
} from '@material-ui/core';

const LatestActions = (props) => {

  const theme = useTheme();

  const formatDate = () => {
      const hours = (props.date.getUTCHours().toString().length === 1) ? `0${props.date.getUTCHours()}` : props.date.getUTCHours()
      const minutes = (props.date.getUTCMinutes().toString().length === 1) ? `0${props.date.getUTCMinutes()}` : props.date.getUTCMinutes()
      const seconds = (props.date.getUTCSeconds().toString().length === 1) ? `0${props.date.getUTCSeconds()}` : props.date.getUTCSeconds()
      return `${hours}:${minutes}:${seconds}`
  }

  return (
    <Card {...props}>
      <CardHeader titleTypographyProps={{fontSize: 50}} title={formatDate()} />
      <Divider />
      <CardContent>
        <Table>
          <TableBody>
            {props.data.map((action, index) => {
              const date = new Date(action.date)
              const dateColor = (action.description.includes("abriu a aplicao movel") ? "#04d500" : "#b0b700")
              return (
                <TableRow
                  hover
                  key={index}
                >
                  <TableCell style={{fontSize: 18}}>
                    {action.description}
                  </TableCell>
                  <TableCell style={{fontWeight: "bold", fontSize: 20, color: dateColor}}>
                    @{`${date.getUTCHours()}:${date.getUTCMinutes()}:${date.getUTCSeconds()}`}
                  </TableCell>

                </TableRow>
              )
            })}
          </TableBody>
        </Table>
      </CardContent>
    </Card>
  );
};

export default LatestActions;
