/* eslint-disable */
import React, {useEffect, useState} from "react";
import { Helmet } from 'react-helmet';
import {
  Box,
  Container,
  Grid,
  Pagination
} from '@material-ui/core';
import CategoryListToolbar from 'src/components/product/CategoryListToolbar';
import CategoryCard from 'src/components/product//CategoryCard';
import products from 'src/__mocks__/products';
import {HOST} from "../host";
import axios from "axios";
import NavItem from "../components/NavItem";

const CategoriesList = () => {

  const [categories, setCategories] = useState([])

  useEffect(() => {
    axios.get(`${HOST}/custom-admin-api/get-categories/`).then((response) => {
      let tempData = response.data
      setCategories(response.data)

      if (!(JSON.stringify(tempData) === localStorage.getItem('sidebar_categories'))) {
        localStorage.setItem('sidebar_categories', JSON.stringify(tempData))
      }
    });
  }, [])

  return (<>
    <Helmet>
      <title>Categories | Admin</title>
    </Helmet>
    <Box
      sx={{
        backgroundColor: 'background.default',
        minHeight: '100%',
        py: 3
      }}
    >
      <Container maxWidth={false}>
        <CategoryListToolbar/>
        <Box sx={{pt: 3}}>
          <Grid
            container
            spacing={3}
          >
            {categories.map((category) => (
              <Grid
                item
                key={category.id}
                lg={4}
                md={6}
                xs={12}
              >
                <CategoryCard category={category}/>
              </Grid>
            ))}
          </Grid>
        </Box>
      </Container>
    </Box>
  </>)
};

export default CategoriesList;
